.MovieTile {
	display: flex;
	border: 1px solid white;
}
.MovieTile .poster {
	width: 120px;
	height: 180px;
	object-fit: cover;
}

.popper {
	position: absolute;
	background: #000;
	color: white;
	display: block;
	border-radius: 5px;
	overflow: hidden;
	font-size: 1rem;
	margin: 0;
}
.popper li {
	padding: .5em;
}
.popper li:hover {
	background: #fff3;
}
